// Colors
// ---------------------------------

// Default

$color-default-black:               #000000;
$color-default-white:               #ffffff;
$color-transparent:                 rgba(255, 255, 255, 0);

// Project palette
//     https://www.htmlcsscolor.com/hex/334482

$color-neon-blue:                   #2c39f2;

// for intro-screen and video button gradient
$color-venetian-red:                #b80027;
$color-dark-cerulean:               #003d85;

// video button border and hover card button gradient
$color-torch-red:                   #ed0233;  //also super-games and promo bg
$color-cobalt:                      #003eb7;

// hover membership card border gradient also promo small cards border
// red color - $color-crimson
$color-smalt:                       #00359f;

// advantages numbers gradient
// red color - $color-crimson
$color-cobalt-number:               #0041ca;

// main colors
$color-dark-cerulean-video:         #14367a; // veil on video cover
$color-sapphire:                    #102654; // main blue text color
$color-ghost-white:                 #f8f9fc; // gray section background
$color-crimson:                     #d10d42; // active tab button border and red in advantages gradient
$color-grey:                        #787878; // tab buttons border
$color-chambray:                    #495a7d; // tab card subtitle
$color-egyptian-blue:               #103aae; // tab card price
$color-whisper:                     #e8e8e8; // tab price shadow
$color-resolution-blue:             #1c3374; // special advantages card bg and text color in promo cards and bg in reviews
$color-persian-blue:                #2e35ac; // advantages small text under numbers
$color-light-slate-grey:            #74819c; // border between contacts parts
$color-black-russian:               #030a1a; // footer bg
$color-spindle:                     #b6bdcc; // staff card subtitle
$color-dark-grey:                   #afafaf; // slider buttons
$color-gulf-blue:                   #2a3b63; // card drop-shadow


// background for images
$color-midnight-express:            #000145; // Hero screen photo and super games photo
$color-hawkes-blue:                 #d4d7e3; // gym photo 2nd screen
$color-hawkes-blue-light:           #dddfe2; // video cover
$color-link-water:                  #c5cbd6; // staff cards
$color-royal-blue:                  #4777e2; // promo block photo
$color-shuttle-grey:                #59626e; // review photo

// Gradient

$black-to-right:                    linear-gradient(90deg, rgba(0, 0, 0, 0.75) 0%, rgba(0, 0, 0, 0) 75%);

// Typography
// ---------------------------------

//    html   
$fz-default:                        16px;

//     ios     
// https://www.artlebedev.ru/kovodstvo/sections/159/#13
$ff-rouble:                         "Rouble", "Arial", sans-serif;

$ff-Alumni-Sans:                    "Alumni Sans", "Arial", sans-serif;
$ff-Roboto-Condensed:               "Roboto Condensed", "Arial", sans-serif;

// Animation
// ---------------------------------

$tf-default:                        ease;
$trans-default:                     0.3s $tf-default;

//      $tf-default,     
$trans-modal:                       0.6s cubic-bezier(0.55, 0, 0.1, 1);

$trans-600:                         0.6s $tf-default;

// Viewports
// ---------------------------------

$vp-768:                            768px;
$vp-1200:                           1200px;
$vp-1366:                           1366px;
$vp-1920:                           1920px;

// Retina
// ---------------------------------

$retina-dpi:                        144dpi;
$retina-dppx:                       1.5dppx;
